@each $size, $media_query in $display-breakpoints {
    .hidden {
        &-#{$size} {
            @media #{$media_query} {
                display: none !important;
            }
        }
    }
}

.overflow-hidden {
    overflow: hidden;
}

.overflow-x-hidden {
    overflow-x: hidden;
}

.overflow-y-hidden {
    overflow-y: hidden;
}
